/* arabic */
@font-face {
  font-family: "Almarai";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: local("Almarai Light"), local("Almarai-Light"),
    url(../fonts/Almarai/Almarai-Light.ttf) format("woff2");
  unicode-range: U+0600-06FF, U+200C-200E, U+2010-2011, U+204F, U+2E41,
    U+FB50-FDFF, U+FE80-FEFC;
}
/* arabic */
@font-face {
  font-family: "Almarai";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Almarai"), local("Almarai-Regular"),
    url(../fonts/Almarai/Almarai-Regular.ttf) format("woff2");
  unicode-range: U+0600-06FF, U+200C-200E, U+2010-2011, U+204F, U+2E41,
    U+FB50-FDFF, U+FE80-FEFC;
}
/* arabic */
@font-face {
  font-family: "Almarai";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Almarai Bold"), local("Almarai-Bold"),
    url(../fonts/Almarai/Almarai-Bold.ttf) format("woff2");
  unicode-range: U+0600-06FF, U+200C-200E, U+2010-2011, U+204F, U+2E41,
    U+FB50-FDFF, U+FE80-FEFC;
}

/* latin-ext */
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: local("Open Sans Light"), local("OpenSans-Light"),
    url(../fonts/Open_Sans/OpenSans-Light.ttf) format("woff2");
  unicode-range: U+0100-024F, U+0259, U+1E00-1EFF, U+2020, U+20A0-20AB,
    U+20AD-20CF, U+2113, U+2C60-2C7F, U+A720-A7FF;
}
